@import url(https://fonts.googleapis.com/css?family=Kaushan+Script);


































































































































html,body,.container{height:100%;font-family:'Kaushan Script','Sail', cursive
}
body{background:#e74;overflow:hidden;background-image:-webkit-gradient(linear, left top, left bottom, from(#190e14), color-stop(30%, #0d0d4b), color-stop(80%, #c76075), color-stop(95%, #e9b64b));background-image:linear-gradient(to bottom, #190e14, #0d0d4b 30%, #c76075 80%, #e9b64b 95%);background-image:radial-gradient(circle at center bottom, #e9b64b, #c76075 15%, #0d0d4b 75%, #190e14 90%)
}
div#beach,canvas#canvas{position:absolute;top:0;left:0;width:100%;height:100%
}
div#beach{background-image:url(/static/img/beachsil2.75ed2d8.png);background-position:bottom right;background-size:1700px;background-repeat:no-repeat
}
div#video{position:absolute;right:243px;bottom:200px
}
div#video iframe{width:255px;height:155px
}
#people{position:absolute;bottom:65px;left:40px;width:140px
}
#car{position:absolute;bottom:46px;left:180px;width:230px
}
div#sea{background-color:blue;height:85px;position:absolute;bottom:0;left:0;width:100%;background-image:radial-gradient(circle at center top, #23485a, #0d0246)
}
.merrywrap{position:absolute;right:0px;left:0px;bottom:0px;top:0px;background-color:#d44;-webkit-transition:background-color .5s ease;transition:background-color .5s ease
}
.giftbox{position:absolute;width:300px;height:200px;left:50%;margin-left:-150px;bottom:40px;z-index:10
}
.giftbox>div{background:#34495e;position:absolute
}
.giftbox>div:after,.giftbox>div:before{position:absolute;content:"";top:0
}
.giftbox:after{position:absolute;color:#fff;width:100%;content:"Click Me!";left:0;bottom:0;font-size:24px;text-align:center;-webkit-transform:rotate(-20deg);transform:rotate(-20deg);-webkit-transform-origin:0 0;transform-origin:0 0
}
.giftbox .cover{width:100%;top:0;left:0;height:25%;z-index:2
}
.giftbox .cover:before{position:absolute;height:100%;left:50%;width:50px;-webkit-transform:translateX(-50%);transform:translateX(-50%);background:#fdc56d
}
.giftbox .cover>div{position:absolute;width:50px;height:50px;left:50%;top:-50px;-webkit-transform:translateX(-50%);transform:translateX(-50%)
}
.giftbox .cover>div:before,.giftbox .cover>div:after{position:absolute;left:0;top:0;width:100%;height:100%;content:"";-webkit-box-shadow:inset 0 0 0 15px #fdc56d;box-shadow:inset 0 0 0 15px #fdc56d;border-radius:30px;-webkit-transform-origin:50% 100%;transform-origin:50% 100%
}
.giftbox .cover>div:before{-webkit-transform:translateX(-45%) skewY(40deg);transform:translateX(-45%) skewY(40deg)
}
.giftbox .cover>div:after{-webkit-transform:translateX(45%) skewY(-40deg);transform:translateX(45%) skewY(-40deg)
}
.giftbox .box{right:5%;left:5%;height:80%;bottom:0
}
.giftbox .box:before{width:50px;height:100%;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);background:#fdc56d
}
.giftbox .box:after{width:100%;height:30px;background:rgba(0,0,0,0.2)
}
.icons{position:absolute;left:10px;height:auto;-webkit-transform:translateY(10px) rotate(-20deg);transform:translateY(10px) rotate(-20deg)
}
.icons .row{width:100%;text-align:center
}
.icons .row span{color:#e5e5e5;text-shadow:4px 4px 0 rgba(96,125,139,0.4);font-size:50px;display:inline-block;opacity:0;-webkit-transition:opacity 0.7s, -webkit-transform 0.5s ease-in;transition:opacity 0.7s, -webkit-transform 0.5s ease-in;transition:transform 0.5s ease-in, opacity 0.7s;transition:transform 0.5s ease-in, opacity 0.7s, -webkit-transform 0.5s ease-in
}
.step-1 .giftbox{-webkit-animation:wobble 0.5s linear infinite forwards;animation:wobble 0.5s linear infinite forwards
}
.step-1 .cover{-webkit-animation:wobble 0.5s linear infinite  0.1s forwards;animation:wobble 0.5s linear infinite  0.1s forwards
}
.step-1 .icons .row span{opacity:1
}
.step-2 .giftbox:after{opacity:0
}
.step-3 .giftbox,.step-4 .giftbox{opacity:0;z-index:1
}
.step-3 .giftbox:after,.step-4 .giftbox:after{opacity:0
}
.step-2 .giftbox .cover{-webkit-animation:flyUp 0.4s ease-in forwards;animation:flyUp 0.4s ease-in forwards
}
.step-2 .giftbox .box{-webkit-animation:flyDown 0.2s ease-in 0.05s forwards;animation:flyDown 0.2s ease-in 0.05s forwards
}
@-webkit-keyframes wobble{
25%{-webkit-transform:rotate(4deg);transform:rotate(4deg)
}
75%{-webkit-transform:rotate(-2deg);transform:rotate(-2deg)
}
}
@keyframes wobble{
25%{-webkit-transform:rotate(4deg);transform:rotate(4deg)
}
75%{-webkit-transform:rotate(-2deg);transform:rotate(-2deg)
}
}
@-webkit-keyframes flyUp{
75%{opacity:1
}
100%{-webkit-transform:translateY(-1000px) rotate(20deg);transform:translateY(-1000px) rotate(20deg);opacity:0
}
}
@keyframes flyUp{
75%{opacity:1
}
100%{-webkit-transform:translateY(-1000px) rotate(20deg);transform:translateY(-1000px) rotate(20deg);opacity:0
}
}
@-webkit-keyframes flyDown{
75%{opacity:1
}
100%{-webkit-transform:translateY(100%);transform:translateY(100%);opacity:0
}
}
@keyframes flyDown{
75%{opacity:1
}
100%{-webkit-transform:translateY(100%);transform:translateY(100%);opacity:0
}
}
.step-1 .icons .row span{opacity:0
}
.step-1 .icons .row span:first-child{-webkit-transform:translateY(700%) translateX(600%);transform:translateY(700%) translateX(600%)
}
.step-1 .icons .row span:nth-child(2){-webkit-transform:translateY(700%) translateX(500%);transform:translateY(700%) translateX(500%)
}
.step-1 .icons .row span:nth-child(3){-webkit-transform:translateY(700%) translateX(400%);transform:translateY(700%) translateX(400%)
}
.step-1 .icons .row span:nth-child(4){-webkit-transform:translateY(700%) translateX(300%);transform:translateY(700%) translateX(300%)
}
.step-1 .icons .row span:nth-child(5){-webkit-transform:translateY(700%) translateX(200%);transform:translateY(700%) translateX(200%)
}
.step-1 .icons .row span:nth-child(6){-webkit-transform:translateY(700%) translateX(100%);transform:translateY(700%) translateX(100%)
}
.step-1 .icons .row span:nth-child(7){-webkit-transform:translateY(700%) translateX(0);transform:translateY(700%) translateX(0)
}
.step-1 .icons .row span:nth-child(8){-webkit-transform:translateY(700%) translateX(-100%);transform:translateY(700%) translateX(-100%)
}
.step-1 .icons .row span:nth-child(9){-webkit-transform:translateY(700%) translateX(-200%);transform:translateY(700%) translateX(-200%)
}
.step-1 .icons .row span:nth-child(10){-webkit-transform:translateY(700%) translateX(-300%);transform:translateY(700%) translateX(-300%)
}
.step-1 .icons .row span:nth-child(11){-webkit-transform:translateY(700%) translateX(-400%);transform:translateY(700%) translateX(-400%)
}
.step-1 .icons .row span:nth-child(12){-webkit-transform:translateY(700%) translateX(-500%);transform:translateY(700%) translateX(-500%)
}
.step-1 .icons .row span:nth-child(13){-webkit-transform:translateY(700%) translateX(-600%);transform:translateY(700%) translateX(-600%)
}
.step-2 .icons .row span:nth-child(2){-webkit-transition-delay:0.1s;transition-delay:0.1s
}
.step-2 .icons .row span:nth-child(3){-webkit-transition-delay:0.15s;transition-delay:0.15s
}
.step-2 .icons .row span:nth-child(4){-webkit-transition-delay:0.2s;transition-delay:0.2s
}
.step-2 .icons .row span:nth-child(5){-webkit-transition-delay:0.25s;transition-delay:0.25s
}
.step-2 .icons .row span:nth-child(6){-webkit-transition-delay:0.3s;transition-delay:0.3s
}
.step-2 .icons .row span:nth-child(7){-webkit-transition-delay:0.35s;transition-delay:0.35s
}
.step-2 .icons .row span:nth-child(8){-webkit-transition-delay:0.4s;transition-delay:0.4s
}
.step-2 .icons .row span:nth-child(9){-webkit-transition-delay:0.45s;transition-delay:0.45s
}
.step-2 .icons .row span:nth-child(10){-webkit-transition-delay:0.5s;transition-delay:0.5s
}
.step-2 .icons .row span:nth-child(11){-webkit-transition-delay:0.55s;transition-delay:0.55s
}
.step-2 .icons .row span:nth-child(12){-webkit-transition-delay:0.6s;transition-delay:0.6s
}
.step-2 .icons .row span:nth-child(13){-webkit-transition-delay:0.65s;transition-delay:0.65s
}
.step-2 .icons span,.step-3 .icons span,.step-4 .icons span{opacity:1;-webkit-transition-timing-function:cubic-bezier(0, 0, 0.2, 0.91);transition-timing-function:cubic-bezier(0, 0, 0.2, 0.91)
}
.step-4 .icons .row span,.step-3 .icons .row span{color:#c6e2ff;-webkit-animation:neon .08s ease-in-out infinite alternate;animation:neon .08s ease-in-out infinite alternate
}
.step-4 .icons .row span:nth-child(even),.step-3 .icons .row span:nth-child(even){-webkit-animation-duration:0.7s;animation-duration:0.7s
}
@-webkit-keyframes neon{
from{text-shadow:0 0 6px rgba(202,228,225,0.92),0 0 30px rgba(202,228,225,0.34),0 0 12px rgba(30,132,242,0.52),0 0 21px rgba(30,132,242,0.92),0 0 34px rgba(30,132,242,0.78),0 0 54px rgba(30,132,242,0.92)
}
to{text-shadow:0 0 6px rgba(202,228,225,0.98),0 0 30px rgba(202,228,225,0.42),0 0 12px rgba(30,132,242,0.58),0 0 22px rgba(30,132,242,0.84),0 0 38px rgba(30,132,242,0.88),0 0 60px #1e84f2
}
}
@keyframes neon{
from{text-shadow:0 0 6px rgba(202,228,225,0.92),0 0 30px rgba(202,228,225,0.34),0 0 12px rgba(30,132,242,0.52),0 0 21px rgba(30,132,242,0.92),0 0 34px rgba(30,132,242,0.78),0 0 54px rgba(30,132,242,0.92)
}
to{text-shadow:0 0 6px rgba(202,228,225,0.98),0 0 30px rgba(202,228,225,0.42),0 0 12px rgba(30,132,242,0.58),0 0 22px rgba(30,132,242,0.84),0 0 38px rgba(30,132,242,0.88),0 0 60px #1e84f2
}
}
.moon{position:absolute;top:10%;right:200px;width:100px;height:100px;border-radius:50%;background:#ddd;-webkit-box-shadow:inset 20px -10px 0 0 #b9b9b9;box-shadow:inset 20px -10px 0 0 #b9b9b9
}
.moon .crater1{position:absolute;width:30px;height:30px;border-radius:50%;background:#bbb;-webkit-box-shadow:inset -3px 1.5px 0 0 #aaa;box-shadow:inset -3px 1.5px 0 0 #aaa;top:20px;right:20px
}
.moon .crater2{position:absolute;width:10px;height:10px;border-radius:50%;background:#bbb;-webkit-box-shadow:inset -1px 0.5px 0 0 #aaa;box-shadow:inset -1px 0.5px 0 0 #aaa;top:45px;right:50px
}
.moon .crater3{position:absolute;width:15px;height:15px;border-radius:50%;background:#bbb;-webkit-box-shadow:inset -1.5px 0.75px 0 0 #aaa;box-shadow:inset -1.5px 0.75px 0 0 #aaa;top:60px;right:25px
}
@media (min-width: 1000px){
.icons{left:30px
}
.icons .row span{font-size:56px
}
#people{left:80px;bottom:70px;width:160px
}
div#beach{background-size:2000px
}
div#video{right:290px;bottom:235px
}
div#video iframe{width:295px;height:185px
}
.moon{right:230px
}
}

